body {
  color: #060b35;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  height: 408px;
  padding: 0 16px 16px;
  display: flex;
}

.card__image {
  justify-content: center;
  display: flex;
}

.card__image-imac {
  width: 160px;
  height: 134px;
  margin-block: 32px 40px;
}

.card__name {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.price {
  padding-bottom: 16px;
  justify-content: space-between;
  padding-block: 24px 16px;
  display: flex;
}

.price__name {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__costs {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.button {
  justify-content: center;
  display: flex;
}

.button__buy {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  align-items: center;
  width: 166px;
  height: 40px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.button__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.stars__list {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__list > .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  font-size: 10px;
  line-height: 14px;
}
